.members-area-container {
  background: #fff;
  border: 1px solid rgba(0, 0, 0, .1);
  box-shadow: 0 3px 10px rgba(0, 0, 0, .1);
  padding: 20px 30px;
  width: 600px;
  margin: 50px auto 0;

  h1 {
    margin: 0 0 10px;
  }

  .flex-row {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between; /* Safari */
    justify-content:         space-between;
    -webkit-align-items: center; /* Safari */
    align-items:         center;
  }
}


