* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font: 300 14px/1.4 'Helvetica Neue', Helvetica, Arial, sans-serif;
  color: #5c5c5c;
  background: #FFFFFF;
}

a {
  color: #5c5c5c;
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}



